@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-medium{
    padding: 0 .75em;
    height: 2.375em;
    border-radius: 0.25em;
    span{
        font-size: 1em;
        line-height: 150%;
    }
}

.m-btn-small{
    padding: 0 .5em;
    height: 1.9375em;
    border-radius: 0.2em;
    span{
        font-size: .875em;
        line-height: 150%;
    }
    svg,
    img{
        width: .75em;
        height: auto;
        flex-shrink: 0;
        margin-right: .375em;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $black;
    &:hover{
        background-color: darken($primary,10%);
        color: $black;
    }
}
