.fg {
  position: relative;
  margin: 0;
  input {
    border: 1px solid $gray400;
    padding: 0 .75em;
    height: 2.375em;
    width: 100%;
    font-size: 1em;
    line-height: 150%;
    background-color: #fff;
    color: $black;
    border-radius: 0.25em;
    &::placeholder {
      color: $gray600;
    }
    &:focus {
      border-color: $primary;
    }
  }
  label{
    display: block;
    font-size: 1em;
    line-height: 150%;
    margin-bottom: .5em;
  }
  &._required{
    label:after{
      content: '*';
      color: $danger;
      margin-left: .25em;
    }
  }
}

.date-input{
  background-image: url(../img/calendar.svg);
  background-repeat: no-repeat;
  background-position: right .81em center;
  background-size: 1em auto;
  padding-right: 2em;
}

.m-radio {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {

      }
    }
  }
  label {
    padding-left: 1.5em;
    position: relative;
    min-height: 1em;
    font-size: 1em;
    line-height: 150%;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border: 1px solid rgba(0, 0, 0, 0.15);
      width: 1em;
      height: 1em;
      left: 0;
      top: 0;
      transform: translateY(.125em);
      transition: all .100s;
    }
  }
}


.m-radio{
  label{
    &:before{
      border-radius: 50%;
    }
  }
  input{
    &:checked+label{
      &:before{
        border-width: .25em;
        border-color: $primary;
      }
    }
  }
}
