.text40{
  font-size: 2.5em;
  line-height: 120%;
}

.text32{
  font-size: 2em;
  line-height: 120%;
}

.text24{
  font-size: 1.5em;
  line-height: 120%;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 150%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.lh150{
  line-height: 150%;
}

.medium-text{
  font-weight: 500;
}

.bold-text{
  font-weight: 700;
}

.gray-text{
  color: $gray;
}

.gray-text600{
  color: $gray600;
}

.primary-link{
  color: #C89902;
  text-decoration: underline;
  &:hover{
    color: $primary;
  }
}


@media screen and (max-width: 992px){
  .text40{
    font-size: 1.75em;
  }

  .text32{
    font-size: 1.5em;
  }

  .text24{
    font-size: 1.25em;
  }
}
