.footer{
  margin-top: 3em;
  padding: 3em 0;
  background-color: $gray100;
  &__top,
  &__middle{
    display: flex;
    justify-content: space-between;
  }
  &__middle{
    margin-top: 2em;
  }
  &__bottom{
    margin-top: 2em;
    display: flex;
    align-items: center;
  }
  &__logo{
    width: 6.5em;
    margin-right: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__by{
    border-radius: 1.25em;
    border: 1px solid #4B4B4A;
    display: flex;
    align-items: center;
    padding: 0 .625em;
    height: 2.125em;
    color: #4B4B4A;
    &:hover{
      background-color: #4B4B4A;
      color: #fff;
      path{
        fill: #fff;
      }
    }
    img{
      width: 2em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  .site-config{
    margin-left: auto;
  }
}

.footer-contacts,
.footer-socials,
.footer-search{
  &__wrap{
    margin-top: .5em;
    display: flex;
  }
}

.footer-contacts{
  &__col{
    @include lastM(0 3em 0 0);
  }
  &__items{
    margin-top: .5em;
    @include grid100gap(.25em);
  }
}

.footer-socials{
  &__item{
    width: 1.5em;
    flex-shrink: 0;
    @include lastM(0 1em 0 0);
    &:hover{
      img{
        transform: translateY(-20%);
      }
    }
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
  }
}

.footer-search{
  .fg{
    width: 12.4375em;
    flex-shrink: 0;
  }
  &__btn{
    flex-shrink: 0;
    margin-left: 1em;
  }
}

.footer-nav{
  &__links{
    margin-top: .5em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    line-height: 150%;
    @include lastM(0 0 .25em 0);
  }
}

.site-config{
  display: flex;
  align-items: center;
  &__btn{
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__top{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
  }

  .footer-contacts{
    grid-column: 1/-1;
    &__wrap{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    margin-top: 1.5em;
    &__inner{
      position: relative;
      padding-bottom: 6.0625em;
    }
    &__top{
      gap: 1.5em;
      grid-template-columns: 100%;
    }
    &__middle{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
    .text20{
      font-size: 1em;
    }
    &__bottom{
      margin-top: 1.5em;
      flex-direction: column;
      align-items: flex-end;
      position: relative;
    }
    &__logo{
      position: absolute;
      left: 0;
      bottom: 0;
    }
    .site-config{
      order: -1;
      margin-bottom: .88em;
    }
  }

  .footer-contacts{
    &__wrap{
      grid-template-columns: 100%;
      gap: 1em;
      margin-top: 1em;
    }
  }

  .footer-socials{
    order: -1;
  }

  .footer-search{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
}
