.main{
  padding-bottom: 3em;
}

.main-slider{
  position: relative;
  .swiper{
    &-container{
      overflow: hidden;
      border-radius: 1.25em;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-pagination{
      position: absolute;
      bottom: 1.625em;
      &-bullet{
        height: .1875em;
        width: 1.875em;
        background-color: #fff;
        opacity: 0.5;
        margin: 0 .1875em !important;
        &:hover,
        &-active{
          opacity: 1;
        }
      }
    }
    &-button{
      &-prev{
        left: 2.5em;
      }
      &-next{
        right: 2.5em;
      }
    }
  }
  &__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 1.25em;
  }
}

.category-card{
  display: flex;
  flex-direction: column;
  background-color: $gray100;
  border-radius: 1.25em;
  overflow: hidden;
  &__top{
    padding: 1em;
  }
  &__img{
    margin-top: auto;
    width: 100%;
    img{
      display: block;
      width: 100%;
    }
  }
}

.filial-card{
  border-radius: 1.25em;
  background-color: $gray100;
  &__map{
    position: relative;
    padding-bottom: 50%;
    .map{
      position: absolute;
      border-radius: 1.25em 1.25em 0 0;
    }
  }
  &__content{
    padding: 1em;
    @include grid100gap(.5em);
  }
  &__metro{
    display: flex;
    align-items: center;
    i{
      flex-shrink: 0;
      width: 1em;
      height: 1em;
      margin-right: .5em;
      border-radius: 50%;
    }
  }
}

.map{
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.processes-slider,
.portfolio-slider{
  .swiper{
    &-slide{
      width: 25%;
    }
  }
}

.portfolio-card{
  position: relative;
  &__heart{
    position: absolute;
    width: 1.5em;
    top: 1em;
    right: 1em;
    z-index: 2;
    img{
      width: 100%;
      display: block;
    }
  }
  &__img{
    @include imgCover(100%);
    img{
      border-radius: .25em;
    }
  }
}

.balls{
  &__img{
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.advantages{
  @media screen and (min-width: 993px){
    .swiper{
      &-container{
        width: 100%;
      }
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .75em));
        gap: 1.5em;
        transform: unset !important;
      }
      &-slide{
        padding-right: 0;
      }
    }
    .advantage-card{
      height: 100%;
    }
  }
}

.advantage-card{
  border-radius: 1.25em;
  background-color: $gray100;
  padding: 1em;
  min-height: 8em;
  &__top{
    display: flex;
    align-items: flex-start;
    margin-bottom: .5em;
  }
  &__icon{
    width: 1.5em;
    height: 1.5em;
    margin-right: .5em;
    @include center;
    img{
      width: 100%;
      display: block;
    }
  }
}

.news-slider{
  .swiper{
    &-slide{
      width: 50%;
    }
  }
}

.news-card{
  display: flex;
  flex-direction: column;
  &__title{
    min-height: 3em;
  }
  &__content{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__info{
    padding-top: .5em;
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-item{
      display: flex;
      align-items: center;
      img{
        width: 1em;
        flex-shrink: 0;
        margin-right: .5em;
        display: block;
      }
    }
  }
}

.promo-slider{
  .swiper{
    &-slide{
      width: 62.0625em;
    }
  }
}

.promo-card{
  &__img{
    padding-bottom: 25%;
  }
  &__text{
    @include truncate(2);
  }
}

.products-slider,
.certificates-slider{
  .swiper{
    &-slide{
      width: calc(100% / 6);
    }
  }
}

.certificate-card{
  &__img{
    border-radius: 0.25em;
    border: 1px solid $gray600;
    display: block;
    @include imgCover(143.87%);
    img{
      border-radius: inherit;
      display: block;
    }
  }
}

.product-card{
  position: relative;
  &__title{
    @include truncate(2);
  }
  .badge{
    position: absolute;
    z-index: 2;
    top: 1em;
    left: 1em;
    @include center;
    border-radius: 6.25em;
    min-width: 2.725em;
    height: 1.275em;
    padding: 0 .49em;
    color: #fff;
    background-color: $secondary;
  }
}

.socials{
  @media screen and (min-width: 993px){
    &__grid{
      position: relative;
    }
    .swiper{
      &-wrapper{
        @include grid100gap(1em);
        transform: unset !important;
        padding: 1em;
        background-color: $gray100;
        border-radius: 1.25em;
        box-sizing: border-box;
        position: static;
      }
      &-slide{
        width: 100% !important;
        position: static;

      }
    }
    .social-card__img{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      &._active{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}

.social-card{
  @include grid100gap(.25em);
  &__top{
    display: flex;
    align-items: center;
  }
  &__arrow,
  &__icon{
    width: 1em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    margin: 0 .4em;
  }
  &__arrow{
    margin-left: auto;
  }
  &__img{
    position: absolute;
    right: 0;
    top: 0;
    width: calc(100% - (25% - 4.5em / 4) - 1.5em);
    img{
      width: 100%;
      max-height: 100%;
      display: block;
      object-fit: cover;
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding-bottom: 1.5em;
    .container{
      padding: 0;
    }
  }

  .main-slider{
    .swiper{
      &-container{
        border-radius: 0;
      }
      &-button{
        display: none;
      }
    }
    &__img{
      border-radius: 0;
      height: 12.5em;
    }
  }

  .categories{
    .category-card{
      &:nth-child(9)~.category-card{
        display: none;
      }
    }
  }

  .products,
  .promo,
  .certificates,
  .portfolio,
  .processes,
  .news{
    overflow: hidden;
  }

  .products-slider,
  .certificates-slider,
  .promo-slider,
  .processes-slider,
  .portfolio-slider,
  .news-slider{
    overflow: unset;
    .swiper{
      &-slide{
        width: 15.625em;
      }
    }
  }

  .advantages{
    overflow: hidden;
    ._slider{
      overflow: unset;
    }
    .swiper{
      &-slide{
        width: 19.5625em;
      }
    }
  }

  .promo-slider,
  .news-slider{
    .swiper{
      &-slide{
        width: 19.5625em;
      }
    }
  }

  .certificates-slider{
    .swiper{
      &-slide{
        width: 11.75em;
      }
    }
  }

  .socials{
    overflow: hidden;
    &__grid{
      display: block;
    }
    .swiper{
      &-slide{
        width: 16.5em;
        padding-right: 1.5em;
      }
    }
  }

  .social-card{
    &__arrow{
      display: none;
    }
    &__img{
      position: static;
      margin-top: .25em;
      width: 100%;
      img{
        max-height: unset;
        border-radius: 1.25em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .categories{
    .category-card{
      &:nth-child(4)~.category-card{
        display: none;
      }
    }
  }

  .balls{
    &__img{
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
  }

  .socials{
    .swiper{
      &-container{
        width: 16.5em;
        margin: 0 auto;
        transform: translateX(.75em);
      }
      &-slide{
        width: 100%;
      }
    }
  }

  .social-card{
    display: flex;
    flex-direction: column;
    gap: unset;
    align-items: center;
    &__top{
      margin-bottom: .25em;
    }
    &__img{
      margin-top: .5em;
    }
  }
}
