.account{
  padding-bottom: 3em;
  &__grid{
    margin-top: 2em;
  }
  &__content{
    grid-column: 2/-1;
  }
}

.account-sidebar{
  position: sticky;
  top: 1em;
  padding: 1em;
  border-radius: 1.25em;
  background-color: $gray100;
  &__show,
  &__link{
    display: flex;
    align-items: center;
    min-height: 2.5em;
    padding: .5em 1em;
    transition: all .3s;
    color: $gray700;
    &:hover{
      background-color: $yellow100;
      color: $black;
      cursor: pointer;
    }
    img{
      width: .875em;
      flex-shrink: 0;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
  &__icon{
    margin-right: .5em;
  }
  &__arrow{
    margin-left: auto;
    transition: all .4s;
  }
  &__hidden{
    padding: .75em .5em .5em 2em;
    display: none;
  }
  &__links{
    @include grid100gap(.5em);
  }
  &__show{
    &._active{
      background-color: $yellow100;
      .account-sidebar__arrow{
        transform: rotate(-180deg);
      }
    }
  }
}

.user-data{
  @include grid100gap(3em);
  max-width: 26em;
  &__fields{
    margin-top: 1.5em;
    @include grid100gap(1em);
  }
  &__nav{
    display: flex;
  }
}

.radios-group{
  &__wrap{
    display: flex;
    &:not(:first-child){
      margin-top: .5em;
    }
  }
  .m-radio{
    margin-right: 1em;
  }
}


@media screen and (max-width: 992px){
  .account{
    padding: 1em 0 1.5em;
    &__grid{
      margin-top: .75em;
      grid-template-columns: 100%;
    }
    &__content{
      grid-column: 1/-1;
    }
  }

  .user-data{
    gap: 1.5em;
  }
}
