@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper{
  &-button{
    width: 2em;
    height: 2em;
    opacity: .5;
    img{
      width: 100%;
      display: block;
    }
    &.swiper-button-disabled{
      opacity: .5;
    }
    &:hover{
      opacity: 1;
    }
  }
}

.grid-column2,
.grid-column4,
.grid-column6{
  display: grid;
  gap: 1.5em;
}

.grid-column2{
  grid-template-columns: repeat(2,calc(50% - .75em));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
}

.grid-column6{
  grid-template-columns: repeat(6,calc(100% / 6 - 1.5em * 5 / 6));
}

._section{
  padding: 3em 0;
  &__grid{
    margin-top: 1.5em;
  }
}

._slider{
  margin-top: 1.5em;
  overflow: hidden;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.5em);
    }
    &-slide{
      padding-right: 1.5em;
    }
    &-button{
      margin: 0;
      transform: translateY(-50%);
      &-prev{
        left: 1em;
      }
      &-next{
        right: 1em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .grid-column6{
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  }

  ._section{
    padding: 1.5em 0;
  }

  ._slider{
    .swiper{
      &-button{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .grid-column2{
    grid-template-columns: 100%;
  }

  .grid-column6{
    grid-template-columns: repeat(2,calc(50% - .75em));
  }
}
