._card{
  position: relative;
  transition: all .3s;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    border: 1px solid $primary;
    opacity: 0;
    transition: inherit;
    z-index: 2;
    pointer-events: none;
  }
  &:hover{
    color: $black;
    &:before{
      opacity: 1;
    }
  }
}

._card1{
  border-radius: 1.25em;
  background-color: $gray100;
  display: block;
  &__img{
    @include imgCover(50%);
    img{
      border-radius: 1.25em 1.25em 0 0;
    }
  }
  &__content{
    padding: 1em;
    min-height: 4.375em;
  }
}
