.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;

    &__bg {
        background: rgba(#000, 0.2);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &--active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 23.4375em;
        border-radius: 0.3em 0.3em 0 0;
        border: 1px solid rgba(0, 0, 0, 0.20);
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        overflow-y: auto;
        max-height: calc(100 * var(--vh));
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: .75em .5em .75em 1em;
    }
    &__close {
        width: 2em;
        background-color: transparent;
        opacity: .5;
        svg {
            width: 100%;
            height: auto;
        }

        &:hover {
            opacity: 1;
        }
    }
    &__content{
        padding: 1em;
        border-top: 1px solid rgba(0, 0, 0, 0.20);
    }
}

.config-modal{
    &__groups{
        @include grid100gap(1em);
    }
}


.config-group{
    &__radios{
        margin-top: .5em;
        @include grid100gap(.375em);
    }
}
